/* stylelint-disable */
@font-face {
  font-family: "CoFoSans";
  src: url("../fonts/../fonts/CoFoSans-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "CoFoSans";
  src: url("../fonts/../fonts/CoFoSans-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
body {
  font-family: "CoFoSans";
  font-size: 16px;
  font-weight: 400;
  color: #8c94a8;
  line-height: 1.5;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.container {
  max-width: 1350px;
  padding: 0 15px;
  margin: 0 auto;
}

.page {
  flex: 1 1 auto;
}

.page-bg {
  background-color: #f4f6fb;
  padding-bottom: 40px;
}

._blue {
  background-color: #007bfb !important;
  color: #fff !important;
}

.logo {
  width: 100px;
}
@media (max-width: 767px) {
  .logo {
    width: 80px;
  }
}
@media (max-width: 420px) {
  .logo {
    width: 60px;
  }
}
.logo img {
  max-width: 100%;
}

h1 {
  font-size: 40px;
  line-height: 1.2;
  font-weight: 500;
  letter-spacing: -0.015em;
  color: #2a2a30;
}
@media (max-width: 1350px) {
  h1 {
    font-size: calc(24px + 16 * (100vw - 320px) / 1030);
  }
}
@media (max-width: 767px) {
  h1 {
    line-height: 1.16;
  }
}

p,
strong {
  letter-spacing: -0.01em;
}

.date {
  color: #8c94a8;
  font-size: 14px;
}

.title {
  margin: 20px 0 30px;
}
@media (max-width: 767px) {
  .title {
    margin: 15px 0 15px;
  }
}

.invisible-text {
  opacity: 0;
  visibility: hidden;
  transform: translate(0px, 50%);
  transition: all 0.8s ease 0.5s;
}

.visible-text {
  opacity: 1;
  transform: translate(0px, 0px);
  visibility: visible;
  transition: all 0.8s ease 0.5s;
}

.invisible {
  opacity: 0;
  visibility: hidden;
  transition: all 0.8s ease 0.3s;
}

.visible {
  opacity: 1;
  visibility: visible;
  transition: all 0.8s ease 0.3s;
}

.box-shadow {
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.08);
}

.header-close {
  transform: translate(0px, -40px);
}

.header-open {
  transform: translate(0px, 0px);
}

.header-top {
  background-color: #f4f6fb;
  height: 40px;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.header-top__link {
  width: 100%;
  color: #8c94a8;
  font-size: 14px;
  transition: all 0.3s ease 0s;
}
.header-top__link::before {
  content: "‹";
  font-size: 20px;
  padding-right: 10px;
  display: inline-block;
  transition: all 0.5s ease 0s;
}
@media (any-hover: hover) and (any-pointer: fine) {
  .header-top__link:hover {
    transition: all 0.3s ease 0s;
    color: #007bfb;
  }
  .header-top__link:hover::before {
    transform: translateX(-10px);
    transition: all 0.5s ease 0s;
  }
}

.header-body {
  background-color: #fff;
  border-radius: 0 0 32px 32px;
  z-index: 1;
  transition: all 1s ease 0s;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

.header-nav {
  margin: 15px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-nav__menu {
  width: 100%;
}
.header-nav__menu-list {
  display: flex;
  justify-content: flex-end;
  padding-right: 60px;
}
@media (max-width: 767px) {
  .header-nav__menu-list {
    padding-right: 0px;
  }
}
@media (max-width: 420px) {
  .header-nav__menu-list {
    justify-content: flex-start;
  }
}
.header-nav__menu-list li {
  padding-right: 20px;
}
@media (max-width: 767px) {
  .header-nav__menu-list li {
    padding-right: 10px;
  }
}
@media (max-width: 420px) {
  .header-nav__menu-list li:last-child {
    padding-right: 0;
  }
}
.header-nav__menu-list li a {
  font-size: 14px;
  font-weight: 500;
  color: #282d3c;
}
@media (any-hover: hover) and (any-pointer: fine) {
  .header-nav__menu-list li a:hover {
    color: #007bfb;
    transition: all 0.3s ease 0s;
  }
}
.header-nav__menu-list li:first-child a {
  color: #8c94a8;
}

.header-logo {
  margin-right: 20px;
}
@media (max-width: 420px) {
  .header-logo {
    display: none;
  }
}

.nav {
  position: relative;
  display: flex;
  padding-bottom: 30px;
}
@media (max-width: 420px) {
  .nav {
    padding-bottom: 15px;
  }
}
.nav__list {
  display: flex;
}
@media (max-width: 420px) {
  .nav__list {
    display: none;
  }
  .nav__list.menu--active {
    position: absolute;
    top: 50px;
    left: -15px;
    display: inline-block;
    background-color: #fff;
    width: 78px;
    padding: 10px 0 10px;
    border-radius: 0 32px 32px 32px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.08);
  }
  .nav__list.menu--active a {
    margin-bottom: 10px;
    margin-left: 15px;
  }
}
.nav a {
  font-size: 12px;
  font-weight: 500;
  color: #282d3c;
  padding: 0 18px;
  height: 48px;
  width: 60px;
  border-radius: 28px;
  background-color: #f4f6fb;
  margin-right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.4s ease 0s;
}
@media (any-hover: hover) and (any-pointer: fine) {
  .nav a:hover {
    border-radius: 8px;
    background-image: radial-gradient(#fff, rgba(0, 0, 0, 0.061));
    transition: all 0.4s ease 0s;
  }
  .nav a:hover._blue {
    background-image: none;
    background-color: #55a5fb !important;
  }
}
@media (max-width: 420px) {
  .nav a {
    width: 48px;
  }
}

.burger {
  display: none;
}
@media (max-width: 420px) {
  .burger {
    display: block;
    position: absolute;
    top: 7px;
    left: 65px;
    background-color: inherit;
    border: none;
    padding: 0;
    width: 50px;
    height: 30px;
    cursor: pointer;
  }
  .burger::before, .burger::after {
    content: "";
    position: absolute;
    left: 0;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background-color: #007bfb;
  }
  .burger::before {
    left: 10px;
  }
  .burger::after {
    left: 30px;
  }
  .burger__line {
    position: absolute;
    left: 20px;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background-color: #007bfb;
  }
}

.breadcrumbs {
  display: flex;
}
@media (max-width: 420px) {
  .breadcrumbs {
    display: none;
  }
}
.breadcrumbs li {
  padding-right: 12px;
  text-overflow: ellipsis;
  overflow: hidden;
  max-width: 250px;
  white-space: nowrap;
}
.breadcrumbs li a {
  color: #8c94a8;
  font-size: 12px;
  line-height: 1;
}
@media (any-hover: hover) and (any-pointer: fine) {
  .breadcrumbs li a:hover {
    color: #007bfb;
    transition: all 0.3s ease 0s;
  }
}
.breadcrumbs li:not(:first-child)::before {
  content: "•";
  color: #bcc3d2;
  padding-right: 10px;
  font-size: 12px;
  line-height: 1;
}
.breadcrumbs li:last-child a {
  color: #bcc3d2;
}

.footer {
  background-color: #fff;
  border-radius: 32px 32px 0 0;
  position: relative;
  margin-top: -40px;
}
.footer__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 40px 0;
}
@media (max-width: 992px) {
  .footer__inner {
    flex-wrap: wrap;
  }
}
@media (max-width: 767px) {
  .footer__inner {
    margin: 20px 0;
  }
}
.footer__copy {
  white-space: nowrap;
  flex-shrink: 0;
}
.footer__info {
  line-height: 12px;
  font-size: 8px;
  text-transform: uppercase;
  margin: 0 50px;
  width: 100%;
  flex-grow: 1;
}
@media (max-width: 992px) {
  .footer__info {
    order: -1;
    margin: 0;
    margin-bottom: 20px;
  }
}
.footer__info a {
  color: #bcc3d2;
}
.footer__logo {
  flex-shrink: 0;
}

.page-nav {
  padding-top: 120px;
  background-color: #fff;
  border-radius: 0 0 32px 32px;
}
@media (max-width: 420px) {
  .page-nav {
    padding-top: 80px;
  }
}

.posts {
  display: flex;
  flex-wrap: wrap;
  padding: 30px 0 0 0;
  margin-right: -3%;
}

.posts-btn {
  width: 276px;
  height: 48px;
  margin: 10px auto 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 28px;
  padding: 10px;
  font-size: 14px;
  color: #007bfb;
  font-weight: 500;
  background-color: #fff;
  transform: perspective(1px) translateZ(0);
  position: relative;
  overflow: hidden;
  transition: color 0.5s ease 0s;
}
.posts-btn:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: -76px;
  background: #007bfb;
  transform: scale(0);
  transition: transform 0.5s ease-out 0s;
  width: 200px;
  height: 200px;
  border-radius: 50%;
}
@media (any-hover: hover) and (any-pointer: fine) {
  .posts-btn:hover {
    border-radius: 8px;
    color: #fff;
    transition: all 0.5s ease 0s;
  }
  .posts-btn:hover:before {
    transform: scale(2);
  }
}
@media (max-width: 767px) {
  .posts-btn {
    width: 100%;
  }
}
@media (max-width: 420px) {
  .posts-btn {
    margin-bottom: 30px;
  }
}

.post-card {
  flex: 0 1 47%;
  margin-right: 3%;
  margin-bottom: 3%;
  background-color: #fff;
  border-radius: 28px;
  padding: 20px 50px 20px 20px;
  min-height: 340px;
  transition: all 0.8s ease 0s;
}
@media (max-width: 767px) {
  .post-card {
    flex: 0 1 100%;
    padding-right: 20px;
    min-height: auto;
  }
}
.post-card__inner {
  height: 100%;
  max-width: 384px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .post-card__inner {
    max-width: 100%;
  }
}
.post-card__title {
  font-size: 32px;
  line-height: 36px;
  font-weight: 500;
  letter-spacing: -0.015em;
  color: #282d3c;
  padding-bottom: 30px;
}
@media (max-width: 1350px) {
  .post-card__title {
    font-size: calc(20px + 12 * (100vw - 320px) / 1030);
    line-height: calc(24px + 12 * (100vw - 320px) / 1030);
  }
}
.post-card__text {
  color: #8c94a8;
  flex: 1 1 auto;
  margin-bottom: 20px;
}
@media (max-width: 767px) {
  .post-card__text {
    max-width: 384px;
  }
}
@media (any-hover: hover) and (any-pointer: fine) {
  .post-card:hover {
    background-color: #007bfb;
  }
  .post-card:hover .post-card__title {
    color: #fff;
    transition: all 0.5s ease 0s;
  }
  .post-card:hover .post-card__text,
.post-card:hover .date {
    color: rgba(255, 255, 255, 0.582);
  }
}

.page-single {
  padding-top: 120px;
}
@media (max-width: 992px) {
  .page-single {
    margin-bottom: -60px;
  }
}
@media (max-width: 420px) {
  .page-single {
    padding-top: 90px;
  }
}
.page-single__inner {
  display: flex;
  align-items: flex-start;
  padding-bottom: 70px;
  margin-top: 20px;
}
@media (max-width: 992px) {
  .page-single__inner {
    flex-direction: column;
  }
}
.page-single__content {
  width: 70%;
}
@media (max-width: 992px) {
  .page-single__content {
    width: 100%;
  }
}
.page-single__content span {
  margin: 16px 0 32px;
  display: block;
}
.page-single__content p,
.page-single__content strong {
  margin-bottom: 20px;
}
.page-single__sidebar {
  width: 416px;
  margin-left: 110px;
  height: auto;
}
@media (max-width: 1125px) {
  .page-single__sidebar {
    margin-left: 60px;
  }
}
@media (max-width: 992px) {
  .page-single__sidebar {
    margin-left: -15px;
    width: calc(100% + 30px);
  }
}

.sidebar {
  background-color: #f4f6fb;
  border-radius: 32px;
  padding: 32px 32px 12px;
}
@media (max-width: 992px) {
  .sidebar {
    padding-bottom: 32px;
  }
}
.sidebar__title {
  font-weight: 500;
  font-size: 20px;
  color: #282d3c;
  margin-bottom: 25px;
}
.sidebar__post span {
  display: block;
  margin-bottom: 20px;
}
.sidebar__text {
  color: #282d3c;
  line-height: 20px;
  margin-bottom: 8px;
  transition: all 0.3s ease 0s;
}
@media (any-hover: hover) and (any-pointer: fine) {
  .sidebar__text:hover {
    color: #007bfb;
    transition: all 0.3s ease 0s;
  }
}