/* stylelint-disable */
// Примерение , вставить в нужное место @include text-h1;
@mixin text-h1 {
  font-size: 40px;
  line-height: 1.2;
  font-weight: 500;
  letter-spacing: -.015em;
  color: $H;
  
  @media (max-width: 1350px) {
    font-size: calc(24px + (40 - 24) * ((100vw - 320px) / (1350 - 320)));
  }
   @media (max-width: 767px) {
     
       line-height: 1.16;
    }
}
@mixin text-h3 {
  font-size: 32px;
  line-height: 36px;
  font-weight: 500;
  letter-spacing: -.015em;
  color: $title;
  @media (max-width: 1350px) {
    font-size: calc(20px + (32 - 20) * ((100vw - 320px) / (1350 - 320)));
    line-height: calc(24px + (36 - 24) * ((100vw - 320px) / (1350 - 320)));
  }
}
